#Yo {
	margin: auto;
	width: 95%;
	height: 95%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.child {
	min-height: 50px;
	min-width: 100px;
	margin: 5px;
}
#title {
	width: 100%;
	height: 60px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}
#perfil {
	height: 220px;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}
#ip {
	border: solid 1px;
	border-radius: 5px;
	height: 90%;
	width: 170px;
}
#info {
	height: 90%;
	width: 350px;
}
#info h3 {
	margin: 10px;
}
#certificados {
	height: 120px;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}
#certificados h3 {
	margin-bottom: 20px;
}
#certificados p {
	font-size: 20px;
	text-align: center;
}


