#Presentation {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 200px;
	height: 225px;
	background-color: white;
	border-radius: 10px;
}
#Presentation img {
	width: 150px;
	border-radius: 50%;
}
#Presentation h2 {
	font-size: 22px;
}
#Presentation h3 {
	padding-top: 5px;
	font-size: 14px;
}


