#Target {
	height: 300px;
	width: 650px;
	border-radius: 5px;
	background-color: #F2F2F2;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}
@media (max-width: 425px) {
	#Target {
		-ms-flex-direction: column;
		    flex-direction: column;
		width: 100%;
		height: 100%;
	}
}


