#Portal {
	border: solid 1px;
	height: 450px;
	width: 600px;
	position: fixed;
	margin: auto;
	z-index: 3;
	background-color: white;
	border-radius: 5px;
    -webkit-animation: portal .4s 1;
            animation: portal .4s 1;
    display: -ms-flexbox;
    display: flex;
}
@-webkit-keyframes portal {
	0% {opacity: 0}
	100% {opacity: 1}
}
@keyframes portal {
	0% {opacity: 0}
	100% {opacity: 1}
}
#Portal button {
	position: absolute;
	top: 25px;
	right: 25px;
	cursor: pointer;
	outline: none;
	background-color: transparent;
	font-size: 22px;
	color: #686868;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
}
#Portal button:hover {
	color: #FF4848;
}
@media (max-width: 425px) {
	#Portal {
		width: 100%;
		height: 100%;
	}
}


