.Options-por {
	border: solid 1px;
	min-height: 80px;
	width: 100%;
	border-radius: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	cursor: pointer;
	-webkit-transition: .3s;
	-o-transition: .3s;
	transition: .3s;
	margin: 5px;
}
.Options-por:hover {
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}
.Options-por img {
	max-height: 60px;
	max-width: 120px;
}
.Options-por span {
	font-size: 14px;
	margin-left: 50px;

}


