#Options {
	margin-top: 15px;
	height: 125px;
	width: 275px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
}
.Options-op {
	border: solid #686868 1.5px;
	border-radius: 5px;
	height: 100px;
	width: 75px;
	cursor: pointer;
	-webkit-transition: .2s;
	-o-transition: .2s;
	transition: .2s;
	-webkit-animation: showing 1.5s 1;
	        animation: showing 1.5s 1;
  	display: -ms-flexbox;
  	display: flex;
  	-ms-flex-pack: center;
  	    justify-content: center;
  	-ms-flex-align: center;
  	    align-items: center;
  	-ms-flex-direction: column-reverse;
  	    flex-direction: column-reverse;
  	position: relative;
}
@-webkit-keyframes showing {
	0% { opacity: 0; top: 20px; }
	75% { opacity: 1; }
	100% { opacity: 1; top: 0px; }
}
@keyframes showing {
	0% { opacity: 0; top: 20px; }
	75% { opacity: 1; }
	100% { opacity: 1; top: 0px; }
}
.Options-op:hover {
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}
.Options-op:hover .info {
	display: -ms-flexbox;
	display: flex;
}
.Options-op:not(:hover) .info {
	height: 0px;
	opacity: 0;
}
.Options-op:active {
	-webkit-transform: scale(0.9);
	    -ms-transform: scale(0.9);
	        transform: scale(0.9);
}
.Options-op i {
	color: black;
	font-size: 18px;
}
.info {
	width: 80%;
	display: none;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-animation: infos .8s 1;
	        animation: infos .8s 1;
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
}
@-webkit-keyframes infos {
	0% { height: 0px; opacity: 1;}
	100% { height: 25px; opacity: 1; }
}
@keyframes infos {
	0% { height: 0px; opacity: 1;}
	100% { height: 25px; opacity: 1; }
}


